html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  background: #182825;
  display: flex;
  color: white;
  justify-content: center;
  align-items: center;
  min-height: 100vh; }

.form-editor {
  max-width: 600px;
  padding: 16px; }

.header__title {
  font-family: 'Montserrat', sans-serif;
  font-size: 28px;
  text-transform: uppercase; }

.flexButtons {
  display: flex;
  justify-content: space-between; }

[class^="button"] {
  background-color: inherit;
  background-position: 100% 0;
  background-size: 300%;
  cursor: pointer;
  font-size: 16px;
  font-weight: bold;
  transition: .8s; }

[class^="button"]:hover {
  background-position: 0 100%; }

.button {
  border: 2px solid rgba(255, 255, 255, 0.1);
  background-image: linear-gradient(45deg, rgba(255, 255, 255, 0.3) 50%, transparent 50%);
  color: rgba(255, 255, 255, 0.3);
  border-radius: 5%;
  font-family: 'Special Elite', cursive;
  height: 42px;
  margin-top: 18px;
  text-transform: uppercase;
  width: 140px; }
  .button--delete {
    border: 2px solid #BC2C1A;
    color: #BC2C1A; }
    .button--delete:hover {
      background-color: #BC2C1A;
      border: none;
      color: #fff; }
  .button--load {
    border: 2px solid darkolivegreen;
    color: darkolivegreen; }
    .button--load:hover {
      color: #fff;
      border: none;
      background-color: darkolivegreen; }
  .button--save {
    color: darkorange;
    border: 2px solid darkorange; }
    .button--save:hover {
      color: #fff;
      border: none;
      background-color: darkorange; }

.header__textarea {
  background: rgba(255, 255, 255, 0.1);
  border: none;
  color: #b6b3b3;
  outline: none;
  -webkit-box-shadow: 0 2px 10px 1px rgba(0, 0, 0, 0.5);
  box-shadow: 0 2px 10px 1px rgba(0, 0, 0, 0.5); }

::placeholder {
  color: #b6b3b3; }

@media (max-width: 500px) {
  .header__title {
    font-size: 16px; }
  .header__textarea {
    width: 180px; }
  .button {
    font-size: 11px;
    height: 22px;
    width: 55px;
    margin-top: 8px; }
  ::placeholder {
    font-size: 13px; } }
